.nav-home {
    .theme-header {
        position: absolute;
    }
    .home-page-selected {
        #stickyHeader div.menu-header.d-none.d-sm-block > ul > li:nth-child(1) > a {
            color: $green;
        }
    }
    .our-purpose-selected {
        #stickyHeader div.menu-header.d-none.d-sm-block > ul > li:nth-child(2) > a {
            color: $green;
        }
    }
    .local-giving-selected {
        #stickyHeader div.menu-header.d-none.d-sm-block > ul > li:nth-child(3) > a,
        #stickyHeader div.menu-header.d-none.d-sm-block > ul > li.menu-item.menu-item-has-children > ul > li:nth-child(2) > a {
            color: $green;
        }
    }
    .sustainable-giving-selected {
        #stickyHeader > div > div > div > nav > div.menu-header.d-none.d-sm-block > ul > li:nth-child(3) > a,
        #stickyHeader > div > div > div > nav > div.menu-header.d-none.d-sm-block > ul > li.menu-item.menu-item-has-children > ul > li:nth-child(3) > a {
            color: $green;
        }
    }
    .contact-selected {
        #stickyHeader div.menu-header.d-none.d-sm-block > ul > li:nth-child(4) > a {
            color: $green;
        }
    }
    .national-giving-selected {
        #stickyHeader > div > div > div > nav > div.menu-header.d-none.d-sm-block > ul > li:nth-child(3) > a,
        #stickyHeader > div > div > div > nav > div.menu-header.d-none.d-sm-block > ul > li.menu-item.menu-item-has-children > ul > li:nth-child(1) > a {
            color: $green;
        }
    }
}
