.privacy-page {
    margin-bottom: rem(80px);
    .simple-hero-container {
        background-color: $bigstone;
        color: $off-white;
        margin-bottom: rem(40px);
        padding: rem(100px) rem(80px);
    }
    &__content {
        width: 50%;
        margin-left: auto;
        margin-right: auto;
        font-size: rem(15px);
        ul {
            list-style-type: disc;
        }
        ol, ul {
            padding: 0;
            li {
                font-size: rem(16px);
                margin-top: rem(20px);
            }
        }
        .no-bullet-list {
            list-style-type: none;
        }
        .pls-read {
            padding-top: rem(10px);
            font-weight: 700;
        }
    }
}