@import "variables/variables";

@font-face {
    font-family: 'icons';
    src:
        url('#{$icon-path}/icons.ttf?inrjh0') format('truetype'),
        url('#{$icon-path}/icons.woff?inrjh0') format('woff'),
        url('#{$icon-path}/icons.svg?inrjh0#icon}') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icons' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-cheveron {
    &:before {
        content: $icon-cheveron;
    }
}
