.#{$global} {
	&-app {
		min-height: 100vh;
	}

	&-wrap {
		flex: 1 0 auto;
	}

	&-main {
		background-color: $white;
	}
}

.site-overlay {
	// background-color: $bg-site-overlay;
	@extend .w-100;
	@extend .h-100;
	@extend .position-fixed;
	@extend .opacity-0;
	@extend .transition-fast;
	@extend .zindex-n-1;
}


.container {
	max-width: 1440px;
	@include make-container();
	padding-left: 15px;
	padding-right: 15px;
	@include media-breakpoint-up(lg) {
		padding-left: 70px;
		padding-right: 70px;
	}
}
