html{
    font-size: 10px;
}
html,
body {
    height: auto;
    position: relative;
    // overflow-x: hidden;
    -webkit-overflow-scrolling: touch; // Makes the smooth scrolling on ios devices
}

body {
    font-size: rem(16px);
    line-height: rem(26px);
    color: $gray-700;
    @include font-smoothing(on);
    @include media-breakpoint-up(sm) {
        font-size: rem(16px);
        line-height: rem(26px);
    }
    @include media-breakpoint-up(lg) {
        font-size: rem(20px);
        line-height: rem(32px);
        //letter-spacing: -0.02em;
    }
}

a {
    color: $primary;
    @extend .transition-fast;
    text-decoration: none;
    &:hover,
    &:focus {
        color: $tiber;
        text-decoration: none;
    }
}

button,
.btn,
.btn-link {
    text-decoration: none;
    &:hover,
    &:focus {
        text-decoration: none;
        outline: none;
    }
}

figure {
    margin-bottom: 0;
}
