.above-the-fold {
    background-color: $white;
    min-height: 85vh;
    @include media-breakpoint-up(sm) {
        min-height: 90vh;
    }
    @include media-breakpoint-up(md) {
        min-height: 117vh;
    }
    > div {
        min-height: inherit;
        @include media-breakpoint-up(sm) {
            min-height: inherit;
        }
        > .row {
            min-height: inherit;
            @include media-breakpoint-up(sm) {
                min-height: inherit;
            }
        }
    }

    &--image{
        .screen-02{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }

    &-ceo{
        h2{
            margin-bottom: rem(32px);
            @include media-breakpoint-up(md){
                margin-bottom: rem(40px);
            }
        }
    }

    &-giving{
        color: $white;
        min-height: 1vh;
        @include media-breakpoint-up(sm) {
            min-height: 1vh;
        }
        @include media-breakpoint-up(md) {
            min-height: 1vh;
            max-height: 732px;
        }
        .above-the-fold--image{

            min-height: auto;

            @include media-breakpoint-up(md){
                position: absolute;
                left: auto;
                right: 50%;
                top: 0;
                right: 0;
                width: 50%;
                height: 100%;
            }

            img{
                max-width: 100%;
                @include media-breakpoint-up(md){
                    height: 100%;
                    object-fit: cover;
                    width: 100%;
                }
                @include media-breakpoint-up(lg){
                    height: 100%;
                    object-fit: cover;
                    width: 100%;
                    position: absolute;
                }
            }
        }
    }

    &-contact-us{
        margin-bottom: rem(32px);
        @include media-breakpoint-up(md){
            margin-bottom: rem(96px);
        }
    }
}
.above-the-fold-contact-us {
    h1.text-white {
        margin-left: rem(100px);
        @media only screen and (max-width: 500px) {
            margin-left: rem(0);
        }
    }
}