label{
	font-size: rem(14px);
	font-weight: 300;
	line-height: rem(28px);
	color: $gray-700;
	position: absolute;
	top: 0;
	left: 0;
}

textarea{
	display: block;
	overflow: hidden;
	resize: none;
}

select {
	-moz-appearance:none; /* Firefox */
	-webkit-appearance:none; /* Safari and Chrome */
	appearance:none;
}

.form-group{
	position: relative;
}

.form-control{
	height: auto;
	padding: rem(0 2px 16px 2px);
	border: none;
	border-bottom: 1px solid $black;
	font-size: rem(14px);
	line-height: rem(28px);
	border-radius: 0;
	position: relative;
	background-color: transparent;

	&::placeholder {
		color: $sawnsdown;
		opacity: 0;
	}

	&:focus{
		outline: none;
		box-shadow: none;
		background-color: transparent;
	}
}

//
// Animations
//

// Inputs, textarea
.form-control:focus + label, 
.form-control:not(.nice-select):not(:placeholder-shown) + label,
// Select
.form-control .nice-select + .nice-select.open + label,
.select-wrap.active label{
		top: rem(-30px);
		font-size: rem(14px);
		font-weight: 400;
}

.form-text{
	font-size: rem(14px);
	color: $sawnsdown;
	margin-top: rem(16px);
}


// Error
.has-error{
	.form-control{
		border-bottom: 1px solid $color-red;
	}
}

// Nice select
.nice-select{
	width: 100%;
	z-index: 1;

	&.open{
		border-color: #26ffef;
		z-index: 2;
	}

	&:after{
		width: 10px;
		height: 10px;
    border-bottom: 2px solid #999;
		border-right: 2px solid #999;
	}

	.list{
		margin: 0;
		width: 100%;
		background: #112931;
		padding: rem(16px);
		border-bottom: 1px solid #007A88;;

		li{
			padding: 0;
			background-color: transparent;
			font-family: $font-family-sans-serif;
			font-style: normal;
			font-weight: 300;
			font-size: rem(20px);
			line-height: rem(28px);
			color: $sawnsdown;

			&:hover, &:focus, &.option.selected.focus{
				background-color: transparent;
				color: $white;
			}

			+ li{
				padding-top: 10px;
			}
		}
	}
}

.select-wrap:not(.active){
	.nice-select .current{
		visibility: hidden;
	}
}

// Theme form
.form-white{
	label, .form-control{
		color: $sawnsdown;
	}
	.form-control{
		border-color: #007A88;
	}
	.nice-select{
		&:after{
			border-bottom: 2px solid white;
			border-right: 2px solid white;
		}
	}
	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	input[type=number] {
		-moz-appearance: textfield;
	}
}
