.btn {
    // border-width: rem(1.5px);
    border-width: rem(2px);
    border-radius: rem(40px);
    padding: rem(16px 33px);
    font-family: $font-family-base;
    font-size: rem(16px);
    line-height: rem(19px);
    letter-spacing: -0.01em;
    font-style: normal;
    font-weight: 500;
    &:not(.hamburger) {
        //box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
    }
    &.disabled,
    &:disabled {
        @extend .opacity-2;
        cursor: not-allowed;
    }
    &-primary {
        background-color: $primary;
        color: $white;
        &:hover,
        &:focus {
            background-color: $tiber;
            border-color: $tiber;
            color: $white;
        }
    }
    &-secondary {
        background-color: $secondary;
        &:hover,
        &:focus {
            border-color: $black;
            background-color: $white;
            color: $secondary;
        }
    }
    &-ternary {
        background-color: $primary;
        color: $white;
        &:hover,
        &:focus {
            background-color: $tiber;
            border-color: $white;
            color: $white;
        }
    }
    &-white {
        background-color: $white;
        color: $secondary;
        &:hover,
        &:focus {
            background-color: $primary;
            border-color: $white;
            color: $white;
        }
    }
}

a,
button,
.btn {
    @extend .transition-fast;
    &:focus {
        outline: none;
    }
}
