.pushy {
	background-color: $secondary;
	width: 100%;
	position: fixed;
	color: $white;
	height: calc(100vh - 80px);
	top: rem(80px);
	overflow-x: hidden;
	z-index: 9999;
	-webkit-overflow-scrolling: touch;
	@include media-breakpoint-up(lg) {
		width: 50vw !important;
	}

	&--left {
		left: 0;
		transform: translate3d(-100%, 0, 0);
		@include media-breakpoint-up(lg) {
			transform: translate3d(-50vw, 0, 0);
		}
	}

	&-content {
		height: 100%;
		min-height: 100%;
		visibility: hidden;
		transition: visibility 0.2s cubic-bezier(0.16, 0.68, 0.43, 0.99);
		padding: rem(37px 32px 0);

		&-social {
			padding-bottom: rem(70px);
		}
	}

	.menu {
		//background: red;
		list-style: none;
		padding: 0;
		margin: 0;
		font-size: 32px;
		font-weight: 300;
		li{

			&.menu-item-has-children{
				>a{
					display: none;
				}
			}
			a{
				color: $white;
				display: block;
				margin-bottom: 24px;

				&:hover, &.active {
					color: $primary;
				}

			}
		}

		ul{
			margin: 0;
			padding: 0;
			list-style: none;

		}
	}
}

@include media-breakpoint-down(lg) {
	_::-webkit-full-page-media,
	_:future,
	:root .safari_only {
		padding-bottom: rem(135px);
	}
}

/* Menu Movement */
.pushy-open-right {
	overflow: hidden;

	.theme-main,
	.push {
		transform: translate3d(0, 0, 0);
	}

	.theme-header {
		border-bottom: none;
		background-color: $secondary;
		position: fixed !important;
		top: 0;
		left: 0;
		transform: none;
		z-index: 999;
	}

	.pushy {
		transform: translate3d(0, 0, 0);
	}

	.pushy-content {
		visibility: visible;
	}
}

.modal-open.pushy-open-right {
	.theme-header {
		background-color: $secondary;
	}
}

/* Menu Transitions */
.theme-main,
.pushy,
.push {
	transition: transform 0.2s cubic-bezier(0.16, 0.68, 0.43, 0.99);
}

@keyframes fade {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@-webkit-keyframes fade {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}
