.page-contact-us {
	background-color: $off-white;
	.theme-main {
		padding: 14.4rem 0 3.6rem;
		@include media-breakpoint-up(md) {
			padding: 21.6rem 0 21.6rem;
		}
	}

	h6{
		font-size: rem(14px);
	}

	.email-link{
		color: $white;
		&:hover{
			color: $green;
		}
	}

	.icon-email{
		font-size: 12px;
	}

	.contact-us-list-rs-wrap{
		display: flex;
	}

	.contact-us-list-rs{
		display: flex;
		flex-direction: column;
		max-width: 200px;

		+ .contact-us-list-rs{
			padding-left: 20px;
		}

		a{
			font-size: rem(32px);
			color: $white;
			text-decoration: underline;
			&:hover{
				color: $green;
			}
		}
	}
}

.form-box-sucess{
	p{
		font-size: rem(20px);
		color: $white;
		margin-top: 10px;
	}

	@include media-breakpoint-up(sm) {
		display: flex;
		align-items: center;
		p{
			margin-top: 0px;
			margin-left: 30px;
		}
	}
}