.#{$global} {
    &-footer {
        padding: rem(32px 0 42px);
        font-size: rem(12px);
        line-height: rem(16px);
		@include media-breakpoint-up(md){
			padding: rem(60px 0);
		}
		.navbar-brand{
			display: block;
			padding: 0;
			margin: 0;
			margin-bottom: 16px;
			max-width: 170px;
			@include media-breakpoint-up(md){
				margin-bottom: 0;
			}
			img{
				max-width: 100%;
			}
		}

    }
}

.footer {
    &-info {
      color: $white;
        a{
			color: $white;
			&:hover{
				color: $primary;
			}
		}
        .social {
            font-size: rem(22px);
            margin-bottom: 0;
            @include media-breakpoint-up(sm) {
                font-size: rem(20px);

            }
            @include media-breakpoint-up(md) {
                font-size: rem(26px);
            }

			.icon{
				&-youtube{
					font-size: rem(32px);
					line-height: rem(26px);
				}
				&-instagram{
					font-size: rem(26px);
				}
				&-twitter{
					font-size: 24px;
				}
			}
			ul {
				list-style: none;
				margin: 0;
				padding: 0;
				display: flex;
				align-items: flex-start;
				@include media-breakpoint-up(md){
					justify-content: flex-end;
				}
				> li {
					margin-left: rem(32px);
					display: inline-block;
					@include media-breakpoint-up(sm) {
						margin-left: rem(21px);
					}
					@include media-breakpoint-up(md) {
						margin-left: rem(48px);
					}

					&:first-child {
						margin-left: 0;
					}
					&:last-child{

					}
				}
			}
            a {
                text-decoration: none;
				color: #fff;
				&:hover{
					color: $primary;
				}
            }
        }


    }

}

.footer-copyright{
	margin-bottom: 35px;
	@include media-breakpoint-up(md){
		text-align: center;
		margin-bottom: 0;
	}

	span{
		display: block;
		@include media-breakpoint-up(lg){
			display: inline;
		}
	}
}

.footer-link{
	text-decoration: underline;
	margin-left: rem(6px);
	&:hover{
		text-decoration: underline;
	}
	&::after{
		padding-left: rem(6px);
		content: "|";
		color: $white;
	}
}


