.#{$global} {
	&-header {
		background-color: rgba($secondary, .3);
		width: 100%;
		position: relative;
		padding: 27px 0;
		@include media-breakpoint-up(sm){
			padding: 0;
		}
		.navbar {
			padding: 0;

			&-brand {
				width: rem(150px);
				padding: 0;

				img {
					max-width: 100%;
				}
			}
		}

		//Menu
		.menu {
			margin: 0;
			padding: 0;

			&-item {
				display: inline-block;
				position: relative;
				color: $white;
				font-size: rem(14px);
				line-height: rem(17px);
				padding-top: 46px;
				padding-bottom: 57px;
				padding-left: 16px;
				padding-right: 16px;
				letter-spacing: 0.06em;

				&:last-child {
					padding-right: 0;
				}

				&.menu-item-has-children {
					//padding-right: 36px;
					> a {

						&:after {
							margin-left: rem(4px);
							vertical-align: middle;
							font-size: rem(7px);
							margin-top: rem(-3px);
							content: $icon-cheveron;
							border: none;
							font-family: 'icons';
							display: inline-block;
							transition: $global-tranistion;
						}

					}

					&:hover {
						> a {
							&:after {
								transform: rotate(180deg);
								transition: $global-tranistion;
							}
						}

						.menu-dropdown {
							transform: translateY(0);
							opacity: 1;
							pointer-events: all;

							a {
								color: $white;

								&:hover, &.active {
									color: $primary;
								}
							}
						}

						a {
							color: $primary;
						}
					}
				}

				a {
					color: $white;
					text-transform: uppercase;
					transition: $global-tranistion;

					&:hover, &.active {
						color: $primary;
					}
				}
			}

			&-dropdown {
				padding: 16px;
				position: absolute;
				width: 198px;
				top: 100%;
				background-color: rgba($secondary, .3);
				transform: translateY(20px);
				transition: $global-tranistion;
				pointer-events: none;
				opacity: 0;

				.menu-item {
					padding-top: 6px;
					padding-bottom: 6px;
					padding-left: 0;
					padding-right: 0;

					&:first-child {
						padding-top: 0;
					}

					&:last-child {
						padding-bottom: 0;
					}
				}
			}
		}
	}
}

