// Transitions
.transition {
    &-slow {
        @include transition(all 0.5s ease);
    }
    &-fast {
        @include transition(all 0.3s ease);
    }
}

// Truncating the paragraphs
.truncate {
    &--1-line {
        @include ellipsis(20px, 1);
    }

    &--2-line {
        @include ellipsis(50px, 2);
    }

    &--3-line {
        @include ellipsis(70px, 3);
    }
}

// Background Variations
.bg {
    &-cover {
        background-size: cover !important;
    }
    &-position {
        &-center {
            background-position: center center !important;
        }
    }
    &-no-repeat {
        background-repeat: no-repeat !important;
    }
}

// Text Styling
.text {
    &-line-through {
        text-decoration: line-through !important;
    }
    &-underline {
        text-decoration: underline !important;
    }
}

// Link Styling
.link {
    &-underline {
        text-decoration: underline;
        &:hover,
        &:focus {
            text-decoration: none;
        }
    }
    &-underline-pseudo {
        position: relative;
        &::after {
            content: "";
            background-color: $primary;
            width: 100%;
            height: rem(1px);
            position: absolute;
            left: 0;
            bottom: rem(-5px);
            @extend .transition-fast;
        }
        &:hover,
        &:focus {
            &::after {
              opacity: 0;
            }
        }
    }
    &-dark {
        color: $dark;
        &:hover,
        &:focus {
            color: $primary;
        }
    }
}

// Image Full Screen
.full-screen {
    @extend .bg-cover;
    @extend .bg-no-repeat;
    @extend .bg-position-center;
}

// Opacity
.opacity {
    &-0 {
        @include opacity(0);
    }
    &-1 {
        @include opacity(1);
    }
    &-2 {
        @include opacity(0.2);
    }
    &-3 {
        @include opacity(0.3);
    }
    &-4 {
        @include opacity(0.4);
    }
    &-5 {
        @include opacity(0.5);
    }
    &-6 {
        @include opacity(0.6);
    }
    &-7 {
        @include opacity(0.7);
    }
    &-8 {
        @include opacity(0.8);
    }
    &-9 {
        @include opacity(0.9);
    }
}

// Overflow Variations
.overflow {
    &-x {
        overflow-x: hidden !important;
    }
    &-y {
        overflow-y: hidden !important;
    }
}

// Width Variations
.w {
    &-100-vw {
        width: 100vw;
    }
}

// Text Indention For Hiding
.text {
    &-indent {
        text-indent: rem(-9999px) !important;
    }
    &-indent-0 {
        text-indent: 0 !important;
    }
}

// Image Fit
.object {
    &-fit {
        object-fit: cover;
    }
    &-position {
        &-center {
            object-position: center;
        }
    }
}

// Highligted Text
.text-highlight {
    color: $primary;
}

// text weight
.text-weight-700{
    font-weight: 700;
}
.text-weight-500{
    font-weight: 500;
}
.text-weight-400{
    font-weight: 400;
}
.text-weight-300{
    font-weight: 300;
}