.page-content {
    background-color: $off-white;
    .theme-main {
        background-color: transparent;
    }
    .theme-header {
        .nav-item {
            .nav-link {
                color: $secondary;
            }
            .btn-cta {
                background-color: $primary;
                color: $white;
                &:hover,
                &:focus {
                    background-color: $white;
                    border-color: $secondary;
                    color: $secondary;
                }
            }
        }
    }
    .hamburger-inner {
        background-color: $secondary;
        &::before,
        &::after {
            background-color: $secondary;
        }
    }
    &-wrap {
        padding: rem(50px 0);
        overflow: hidden;
        position: relative;
        @include media-breakpoint-up(md) {
            padding: rem(110px 0);
        }
        &-icon {
            width: 100%;
            text-align: right;
            @include media-breakpoint-up(md) {
                position: absolute;
                top: rem(225px);
            }
            img {
                width: rem(225px);
                position: relative;
                right: rem(-20px);
                margin-bottom: 0;
                @include media-breakpoint-up(sm) {
                    width: rem(270px);
                }
                @include media-breakpoint-up(md) {
                    width: rem(380px);
                    right: rem(-40px);
                }
            }
        }
    }
    &-header {
        margin-bottom: rem(60px);
        h1 {
            margin-bottom: 0;
            color: $primary;
        }
    }
    &-article {
        h1,
        h2,
        h3 {
            margin-bottom: rem(30px);
            @include media-breakpoint-up(md) {
                margin-bottom: rem(40px);
            }
            &:first-of-type {
                & + p {
                    font-size: rem(18px);
                    line-height: rem(22px);
                    margin-bottom: rem(30px);
                    @include media-breakpoint-up(sm) {
                        font-size: rem(20px);
                        line-height: rem(24px);
                    }
                    @include media-breakpoint-up(md) {
                        font-size: rem(28px);
                        line-height: rem(36px);
                        letter-spacing: -0.02em;
                        margin-bottom: rem(50px);
                    }
                }
            }
        }
        h4,
        h5,
        h6 {
            margin-bottom: rem(16px);
        }
        p {
            margin-bottom: rem(25px);
            &:last-child {
                margin-bottom: 0;
            }
            & + figure,
            & + h1,
            & + h2,
            & + h3,
            & + h4,
            & + h5,
            & + h6,
            & + ul,
            & + dl,
            & + ol,
            & + table,
            & + div,
            & + section {
                margin-top: rem(50px);
                @include media-breakpoint-up(md) {
                    margin-top: rem(90px);
                }
            }
        }
        p {
            & + .repeater-lists,
            & + .repeater-content {
                margin-top: rem(30px);
                @include media-breakpoint-up(md) {
                    margin-top: rem(50px);
                }
            }
        }
        img {
            max-width: rem(390px);
            display: block;
            margin: 0 auto;
        }
        ul,
        ol,
        dl,
        table,
        .repeater-lists,
        .repeater-content {
            margin-bottom: rem(50px);
            @include media-breakpoint-up(md) {
                margin-bottom: rem(115px);
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
        ul {
            list-style-type: none;
            padding-left: rem(55px);
            > li {
                margin-bottom: rem(17px);
                position: relative;
                padding-left: rem(30px);
                &::before {
                    content: "\2212";
                    position: absolute;
                    left: 0;
                    top: rem(1px);
                    font-family: $font-family-sans-serif;
                    font-size: 20px;
                    line-height: 30px;
                    letter-spacing: -0.02em;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        dl {
            dd {
                margin-bottom: rem(12px);
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        figure,
        .wp-block-image {
            margin-bottom: rem(50px);
            @include media-breakpoint-up(md) {
                margin-bottom: rem(115px);
            }
        }
        table {
            thead,
            tbody {
                td,
                th {
                    border: 1px solid $secondary;
                    padding: rem(22px 30px 16px);
                    text-align: center;
                    @include media-breakpoint-up(sm) {
                        padding: rem(25px 35px 18px);
                    }
                    @include media-breakpoint-up(lg) {
                        padding: rem(30px 45px 19px);
                    }
                }
            }
            thead {
                background-color: $primary;
                color: $white;
                font-family: $font-family-header;
                font-weight: 600;
                font-size: rem(18px);
                line-height: rem(18px);
                text-transform: uppercase;
                @include media-breakpoint-up(sm) {
                    font-size: rem(22px);
                    line-height: rem(20px);
                }
                @include media-breakpoint-up(lg) {
                    font-size: rem(28px);
                    line-height: rem(25px);
                }
            }
        }
        .wp-block-table {
            table {
                width: auto;
            }
        }
    }
}

.repeater {
    &-lists {
        > .row {
            margin-bottom: -32px;
            > .col,
            > [class*="col-"] {
                margin-bottom: rem(32px);
            }
        }
    }
    &-list-item {
        font-family: $font-family-header;
        font-weight: 600;
        font-size: rem(20px);
        line-height: rem(22px);
        text-transform: uppercase;
        color: $primary;
        @include media-breakpoint-up(sm) {
            font-size: rem(22px);
            line-height: rem(24px);
        }
        @include media-breakpoint-up(md) {
            font-size: rem(28px);
            line-height: rem(30px);
        }
        a {
            text-decoration: none;
        }
        figure {
            min-width: rem(30px);
            flex-basis: rem(30px);
            height: rem(28px);
            margin: rem(0 12px 0 0);
            padding: 0;
            img {
                max-width: 100%;
            }
        }
        span {
            display: block;
            height: rem(18px);
            @include media-breakpoint-up(sm) {
                height: rem(20px);
            }
            @include media-breakpoint-up(md) {
                height: rem(24px);
                white-space: nowrap;
            }
        }
        // img {
        //     width: 28px;
        //     max-width: none;
        //     margin: rem(0 16px 0 0);
        // }
    }
    &-content {
        padding-left: rem(25px);
        @include media-breakpoint-up(lg) {
            padding-left: rem(75px);
        }
        ul {
            padding-left: 0;
            margin-bottom: 50px;
        }
        h5 {
            margin-bottom: rem(20px);
        }
        p + h5 {
            margin-top: rem(50px);
        }
    }
}
