//
b,
strong,
.bold {
	font-family: $font-family-base;
	font-weight: 700;
}

p {
	margin-bottom: rem(32px);
	@include media-breakpoint-up(md){
		margin-bottom: rem(40px);

	}

	&:last-child {
		margin-bottom: 0;
	}
}

blockquote{
	position: relative;
	margin-bottom: rem(32px);
	@include media-breakpoint-up(md){
		margin-bottom: rem(40px);

	}
	&:before{
		content: '';
		width: 2px;
		height: 100%;
		position: absolute;
		left: 0;
		background: $primary;
	}
}

.p {
	letter-spacing: -0.02em;

	&-md {
		font-size: rem(16px);
		line-height: rem(24px);
	}

	&-sm {
		font-size: rem(14px);
		line-height: rem(20px);
	}

	&-xs {
		font-size: rem(12px);
		line-height: rem(18px);
	}
}
