.card {
	&-hero {
		&-content {
			padding: rem(144px 0 36px);
			height: 100%;
			@include media-breakpoint-up(sm) {
				padding: rem(256px 0 136px);
			}

			.above-the-fold-giving & {
				padding: rem(112px 0 93px);
				height: 100%;
				@include media-breakpoint-up(sm) {
					padding: rem(216px 0 216px);
				}
			}

			&--title {
				width: 100px;

				.above-the-fold-giving & {
					font-size: rem(48px);
					line-height: rem(48px);
					width: 200px;
					letter-spacing: -0.02em;
					font-weight: 500;
					margin-bottom: rem(24px);
					@include media-breakpoint-up(md){
						font-size: rem(70px);
						line-height: rem(70px);
						margin-bottom: rem(40px);
					}
				}
			}

			&--content {
				.above-the-fold-giving & {
					max-width: 388px;
				}
			}

			&--space {
				height: 57px;
				width: 1px;
				background: $white;
				margin-bottom: 32px;
				@include media-breakpoint-up(md) {
					margin-bottom: 40px;

				}
			}

			h2 {
				margin-bottom: rem(24px);
				@include media-breakpoint-up(sm) {
					margin-bottom: rem(32px);
				}
			}
		}
	}


}
