.section {
	&-h01 {
		padding-top: 40px;
		@include media-breakpoint-up(sm) {
			padding-top: 96px;
		}
		overflow: hidden;

		h3 {
			margin-bottom: 0;
		}

		.read-more-link {
			margin-right: -15px;
			font-size: 14px;
			line-height: 17px;
			text-align: right;
			letter-spacing: 0.06em;
			font-weight: 500;
			color: #fff;
			text-transform: uppercase;

			&:hover {
				color: $primary;

				.btn {
					background: $tiber;
					border-color: $tiber;

					svg path {
						fill: $white;
					}
				}
			}

			.btn {
				width: 60px;
				height: 60px;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				border-radius: 0;
				margin-left: 19px;
				padding: 0;
				font-size: 47px;
				@include media-breakpoint-up(md) {
					width: 100px;
					height: 100px;
					margin-left: 32px;
				}
				@include media-breakpoint-up(lg) {
					width: 144px;
					height: 144px;
				}

				svg {
					width: 20px;
					@include media-breakpoint-up(md) {
						width: 50px
					}
					@include media-breakpoint-up(lg) {
						width: 50px
					}

					path {
						fill: $white;
					}
				}
			}
		}
		&-content {
			div.small {
				letter-spacing: 0.06em;
			}
		}
	}

	&-h02 {
		color: $bigstone;
		font-size: rem(20px);
		line-height: rem(32px);

		.bg-gradient {
			&:first-child {
				background: rgba($primary, .16);
			}

			&:nth-child(2) {
				background: rgba($primary, .10);
			}

			&:last-child {
				background: rgba($primary, .04);
			}
		}

		.medium {
			font-width: 500;
		}

		.light {
			font-weight: 300;
		}

		h3 {
			margin-bottom: 24px;
			font-weight: 700;
			max-width: 180px;
			order: 1;
			@include media-breakpoint-up(md) {
				margin-bottom: 40px;
			}
		}

		&--content {
			margin-bottom: 64px;
			order: 2;
			height: 100%;
			@include media-breakpoint-up(md) {
				margin-bottom: 106px;
			}
		}

		&--button {
			order: 3;

			.btn {
				min-width: 272px;
			}
		}

		&--card {
			padding: 48px 15px;
			@include media-breakpoint-up(md) {
				padding: 96px 5%;

			}
		}
	}

	&-h03 {
		padding-top: 40px;
		padding-bottom: 40px;
		font-size: rem(20px);
		line-height: rem(32px);
		@include media-breakpoint-up(sm) {
			padding-top: 96px;
			padding-bottom: 96px;
		}
	}

	&-form-home {
		padding-top: 40px;
		padding-bottom: 40px;
		font-size: rem(20px);
		line-height: rem(32px);
		@include media-breakpoint-up(md) {
			padding-top: 96px;
			padding-bottom: 171px;
		}
	}

	&-h04 {
		padding-top: 32px;
		@include media-breakpoint-up(sm) {
			padding-top: 64px;
		}

		&-content {
			max-width: 640px;
			margin: 0 auto;
			padding-bottom: rem(136px);

			h3 {
				padding-left: 20px;
				position: relative;
				color: $secondary;
				font-weight: 500;
				font-size: rem(16px);
				line-height: rem(24px);
				margin-bottom: rem(40px);
				@include media-breakpoint-up(md) {
					font-size: rem(20px);
					line-height: rem(32px);
				}
				@include media-breakpoint-up(sm) {
					padding-left: 24px;
				}

				&:before {
					content: '';
					width: 2px;
					height: 100%;
					position: absolute;
					left: 0;
					background: $primary;
				}
			}

			cite {
				display: block;
				font-weight: 400;
				font-size: rem(14px);
				line-height: rem(20px);
				letter-spacing: 0.05em;
				font-style: normal;
				text-transform: uppercase;
				margin-top: rem(24px);
				color: $secondary;

				strong {
					font-weight: 500;
				}
			}

			blockquote {

				font-size: rem(32px);
				margin-bottom: rem(40px);
				line-height: rem(40px);
				letter-spacing: -0.03em;
				padding-left: rem(20px);
				font-weight: 400;
				color: $secondary;
				@include media-breakpoint-up(md) {
					margin-right: -33%;
					font-size: rem(40px);
					margin-bottom: rem(40px);
					line-height: rem(58px);
					padding-left: rem(24px);

				}

				cite {
					display: block;
					font-weight: 400;
					font-size: rem(14px);
					line-height: rem(20px);
					letter-spacing: 0.05em;
					font-style: normal;
					text-transform: uppercase;
					margin-top: rem(24px);

					strong {
						font-weight: 500;
					}
				}
			}

			.section-image-content {
				max-width: none !important;
				width: 100vw !important;
				margin-left: -50vw !important;
				margin-right: -50vw;
				position: relative;
				left: 50%;
				right: 50%;
				margin-bottom: rem(32px);
				@include media-breakpoint-up(sm) {
					margin-bottom: rem(40px);
				}

				&-container {
					max-width: 640px;
					margin: 0 auto;
					font-size: rem(32px);
					line-height: rem(42px);
					letter-spacing: -0.03em;
					color: $white;

					@include media-breakpoint-up(sm) {
						font-size: rem(40px);
						line-height: rem(58px);
					}

					&--wrap {
						padding-top: rem(112px);
						padding-bottom: rem(148px);
						padding-left: rem(15px);
						padding-right: rem(15px);
						@include media-breakpoint-up(md) {
							margin-right: -7%;
							padding-top: rem(96px);
							padding-bottom: rem(126px);
							padding-left: 0;
							padding-right:0;

						}
					}
				}
			}

			.signature {
				margin-top: rem(32px);
				margin-bottom: rem(24px);
				max-width: 280px;
				@include media-breakpoint-up(sm) {
					max-width: 378px;
					margin-top: rem(40px);
				}

				&-name {
					margin-bottom: rem(32px);
					@include media-breakpoint-up(sm) {
						margin-bottom: rem(64px);
					}
				}
			}

		}
	}

	&-h05 {
		background-color: $bigstone;
		min-height: 85vh;
    @include media-breakpoint-up(sm) {
        min-height: 80vh;
    }
    @include media-breakpoint-up(md) {
        min-height: 89vh;
		}
		@include media-breakpoint-up(lg) { 
			min-height: 91vh;
		}
		.container {
			.card-hero-content {
				padding: 15rem 3rem 7rem 0;
				@include media-breakpoint-up(sm) {
					padding: 25rem 3rem 20rem 0px;
				}
				@include media-breakpoint-up(md) {
					padding: 30rem 3rem 20rem 0px;
				}
				@include media-breakpoint-up(lg) {
					padding: 38rem 3rem 20rem 0;
				}
				max-width: 1088px;
				& h2 {
						margin-bottom: 6.4rem;
				}
			}
		}
	}

	&-slider {

		&--title {
			max-width: 640px;
			margin: 0 auto;
			margin-bottom: rem(32px);
			font-size: rem(20px);
			line-height: rem(32px);
			@include media-breakpoint-up(md) {
				font-size: rem(16px);
				line-height: rem(24px);
				margin-bottom: rem(64px);
			}
		}

		&-infinite{
			display: flex;

			align-items: center;
			@extend .list-unstyled;
			margin-bottom: 0;
			display: flex;
			white-space: nowrap;
			flex-flow: row wrap;

			@include media-breakpoint-up(sm){
				display: flex;
				white-space: nowrap;
				flex-flow: row nowrap;
			}

			&--item{
				width: 50%;
				height: auto;
				padding: 4px;
				display: flex !important;
				justify-content: center;
				align-items: center;
				position: relative;
				@include media-breakpoint-up(sm){
					height: 281px;
					width: auto;
					padding: 0 4px;
				}
				&.double{
					width: 100%;
					@include media-breakpoint-up(sm){
						width: auto;
					}
				}
				img{
					height: auto;
					width: 100%;
					@include media-breakpoint-up(sm){
						height: 281px;
						width: auto;
						width: 100%;
						object-fit: contain;
						padding: 5px;
					}
				}
			}

			.click-plus{
				position: absolute;
				width: 16px;
				height: 16px;
				border: none;
				background: none;
				right: 10px;
				top: 10px;
				z-index: 9;
				&:before,
				&:after{
					content: "";
					position: absolute;
					background-color: #b0b0b0;
					transition: transform 0.25s ease-out;
				}

				/* Vertical line */
				&:before{
					top: 0;
					left: 50%;
					width: 1px;
					height: 100%;
					margin-left: 0px;
				}

				/* horizontal line */
				&:after{
					top: 50%;
					left: 0;
					width: 100%;
					height: 1px;
					margin-top: -1px;
				}
			}

			&--link{
				display: block;
				background: #fafafa;
				width: 400px;
				&:hover{
					@include media-breakpoint-up(sm){

						// .section-slider-infinite-caption {
						// 	opacity: 1;
						// }

						// .click-plus {
						// 	&:before {
						// 		transform: rotate(90deg);
						// 	}

						// 	&:after {
						// 		transform: rotate(180deg);
						// 	}
						// }
					}
				}
			}

			&-caption{
				position: absolute;
				width: 100%;
				height: 100%;
				left: 0;
				top: 0;
				background: rgba($bigstone,.7);
				padding: rem(56px 32px);
				word-wrap: normal;
				white-space: normal;
				color: $white;
				opacity: 0;
				transition: $global-tranistion;

				p{
					font-size: rem(16px);
					line-height: rem(24px);
					margin-bottom: rem(32px);
				}

				&--meta{
					font-size: rem(14px);
					line-height: rem(20px);
					strong{
						display: block;
					}
				}
			}
		}

	}



}
